<template>
  <main id="app">
    <img alt="Vue logo" src="./assets/logo.png" />
    <h3>PWA with vue.js - Media Devices project </h3> 
      <br>
    <MediaDevices  />
    <section class="update-dialog" v-if="prompt">
      <article class="update-dialog__content">
        A new version is found. Refresh to load it?
      </article>
      <article class="update-dialog__actions">
        <button
          class="update-dialog__button update-dialog__button--confirm"
          @click="update"
        >
          Update
        </button>
        <button
          class="update-dialog__button update-dialog__button--cancel"
          @click="prompt = false"
        >
          Cancel
        </button>
      </article>
    </section>
  </main>
</template>

<script>
import MediaDevices from "@/components/MediaDevices.vue";
export default {
  name: "App",
  components: {
    MediaDevices
  },
  methods: {
    async update() {
      this.prompt = false;
      await this.$workbox.messageSW({ type: "SKIP_WAITING" });
    }
  },
  data() {
    return {
      prompt: false,
    };
  },
  created() {
    if (this.$workbox) {
      this.$workbox.addEventListener("waiting", () => {
        this.prompt = true;
      });
    }
  }
}
</script>

