<template>
  <section id="camera">
    <article>
      <button id="snap" @click="capture"><img src="../assets/play-solid.svg"></button>
      <button id="snap" v-on:click="stop"><img src="../assets/stop-circle-regular.svg"></button>
    </article>
    <video id="v" ref="v" autoplay ></video>

    <article id="pic">
      <button id="snap" @click="takePic"><img src="../assets/images-regular.svg"></button>
    </article>
    <canvas ref="canvas" id="canvas" width="240" height="180"></canvas>
      <ul>
          <li v-for="(c,index) in captures" :key="index">
              <img  v-bind:src="c" height="50" />
          </li>
      </ul>
  </section>
</template>

<script>
export default {
  name: 'media-devices',
  props:{
  },
  data:()=> {
    return {
      video: {},
      canvas: {},
      captures: []
    }
  },
  mounted() {
    this.stop();
  },
  methods:{
    async capture() {
      if( 'mediaDevices' in navigator ) {
        try {
            this.video = this.$refs.v;
            if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
              await navigator.mediaDevices.getUserMedia({ video:  { width: 320, height: 220 } }).then(stream => {
                this.video.srcObject = stream;
              });
            }//IF nav 
        } catch (error) {
          console.log('Could not show camera window.');
        }//try..catch
      }//IF
    }, // EndOf capture()
    async stop() {
        return await this.$refs.v.srcObject && this.$refs.v.srcObject.getTracks().map(t => t.stop());
    },
    async takePic() {
        this.canvas = await this.$refs.canvas;
        this.canvas.getContext("2d").drawImage(this.video, 0, 0, 240, 180);
        this.captures.push(this.canvas.toDataURL("image/png"));
    },
  },
}
</script>

